body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  text-align: center;
  color: #fff;
  background-color: #333;
  padding: 1rem;
}

main {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}

.search-form {
  margin-bottom: 1rem;
  display: flex;
}

.search-form input[type="text"] {
  flex-grow: 1;
  padding: .5rem;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  padding: .5rem 1rem;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  display: grid;
}

.photo-card {
  text-align: center;
  border: 1px solid #ccc;
  padding: 1rem;
}

.photo-card img {
  max-width: 100%;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  margin: 0 auto;
  padding: .5rem 1rem;
  display: block;
}

